:root {
  --color-blue: #00acdc;
}

body {
  letter-spacing: 0;
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding-inline: 50px;
  display: flex;
}

.nav__link {
  text-align: center;
  color: #000;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.nav__link:hover {
  color: var(--color-blue);
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__list .nav__item:not(:last-child) .nav__link {
  margin-right: 20px;
}

.nav__link.is-active {
  color: var(--color-blue);
}

.nav__link.is-active:after {
  content: "";
  background: var(--color-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.logo {
  display: flex;
}

.nav {
  text-transform: uppercase;
}
